*{
    box-sizing: border-box;
}
body{
    background-color: black;
    color: aliceblue;
}
a{
    color: inherit;
    text-decoration: none;
}
button{
    display: flex;
    align-items: center;
    background-color: inherit;
    color: inherit;
    min-width: 50px;
    max-height: 30px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}
.home-page{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 25px auto;
}
.form-wrap{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 250px;
    margin: 25px auto;
}
.email-form,    
.user-form,
.check-wrap,
.img-text{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    gap: 15px;
    margin: 0 auto;
}
.sign-page{
    display: flex;
    padding: 10px 15px;
    background: #256789;
    margin: 0 10px;
}
.btn-group{
    display: flex;
    gap: 10px;
}
.btn-signUp{
    justify-content: flex-end;
}
.sign{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(13, 204, 204);
    padding: 10px 15px;
    color: rgb(43, 41, 41);
    margin-bottom: 10px;
}
.email-form,
.img-text{
flex-direction: column;
width: 100%;
}
.email,
.password,
.userName{
    display: flex;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    width: 100%;
}
.img-wrap{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    padding: 10px;
    background: #526879;
}
.userName {
    flex-direction: column;
}
input{
    background-color: #8dc9e8;
}

.btn-group_link{
    color:#256789
}
.user-log{
    flex-direction:column;
}